@import './variables';
@import './libs/normalize';
@import './components/all';
@import url('https://fonts.googleapis.com/css2?family=Nunito:wght@400;700&family=Philosopher:ital,wght@0,400;0,700;1,400;1,700&display=swap');

body {
  background-image: url('../assets/img/546896.jpg');
  background-size: cover;
}

.wrapper {
  width: calc(100vw - 100px);
  height: 100%;
  background-color: #837e7e;
  background-color: rgba(72, 50, 50, 0.5);
  margin: 50px auto;
  border-radius: 10px;
  max-width: 1400px;

  @media (max-width: 645px) {
    width: calc(100vw - 30px);
    margin-top: 15px;
  }
}

.content {
  display: flex;
  justify-content: center;
  min-height: 85vh;
  padding: 40px 0;

  &__error-info {
    margin: 100px auto;
    width: 500px;
    text-align: center;

    h2 {
      font-size: 32px;
      font-weight: 700;
      margin-bottom: 10px;
    }

    p {
      font-size: 20px;
    }
  }

  &__title {
    font-weight: 800;
    font-size: 38px;
    margin-top: 60px;
    margin-bottom: 50px;
    @media (max-width: 760px) {
      text-align: center;
    }
  }

  &__items {
    display: grid;
    grid-template-columns: repeat(8, 1fr);
    grid-template-rows: repeat(1, 1fr);

    @media (max-width: 1400px) {
      grid-template-columns: repeat(7, 1fr);
    }
    @media (max-width: 1060px) {
      grid-template-columns: repeat(4, 1fr);
    }
    @media (max-width: 730px) {
      grid-template-columns: repeat(3, 1fr);
    }
  }

  &__top {
    display: flex;
    align-items: center;
    justify-content: space-between;

    @media (max-width: 1295px) {
      .categories {
        width: 100%;
        overflow: auto;

        ul {
          width: 785px;
        }
      }
      .sort {
        padding-left: 20px;

        b {
          display: none;
        }
      }
    }
    @media (max-width: 760px) {
      .categories {
        ul {
          li {
            padding: 8px 20px;
          }
        }
      }
    }
    @media (max-width: 820px) {
      .categories {
        margin-top: 30px;
      }
      flex-direction: column-reverse;
    }
  }
}

.container {
  width: $container-width;
  margin: 0 auto;

  .heroes-block-wrapper{
    img{
      cursor: pointer;
      width: 60px;
    }
    .hero-icon{
      @media (max-width: 1000px) {
        width: 50px;
      }
    }
    .heroes-title{
      margin-bottom: 3px;
      color: white;
      display: flex;
      height: 22px;
      img{
        margin-right: 5px;
        cursor: auto !important;
        width: 20px;
        height: 20px;
      }
    }
    .heroes-block{
      margin: 20px 0;
    }
    .heroes-block:not(:first-child){
      .heroes-title{
        margin-top: 45px;
      }
    }

  }
  .footer {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: column;

    .button {
      margin-bottom: 25px;
      @media (min-width: 1125px) {
        display: none;
      }
    }
  }

  &--cart {
    max-width: 820px;
    margin: 90px auto;
    @media (max-width: 760px) {
      margin: 0 auto;
    }

    .content__title {
      margin: 0;
    }
  }
}

.cart {
  &__top {
    display: flex;
    justify-content: space-between;
    align-items: center;
    @media (max-width: 760px) {
      flex-direction: column;
      justify-content: center;
    }
  }

  .content__title {
    display: flex;
    align-items: center;
    font-size: 32px;
    @media (max-width: 760px) {
      margin-bottom: 40px;
      font-size: 28px;
    }

    svg {
      position: relative;
      top: -2px;
      width: 30px;
      height: 30px;
      margin-right: 10px;

      path {
        stroke: $black;
        stroke-width: 1.9;
      }
    }
  }

  &__clear {
    display: flex;
    align-items: center;
    cursor: pointer;
    @include noselect();

    span {
      display: inline-block;
      margin-left: 7px;
      color: #b6b6b6;
      font-size: 18px;
    }

    span,
    svg,
    path {
      transition: all $duration ease-in-out;
    }

    &:hover {
      svg {
        path {
          stroke: darken($color: #b6b6b6, $amount: 50);
        }
      }

      span {
        color: darken($color: #b6b6b6, $amount: 50);
      }
    }
  }

  .content__items {
    display: flex;
    flex-direction: column;
  }

  &__item {
    display: flex;
    justify-content: space-between;
    width: 100%;
    border-top: 1px solid $gray-line;
    padding-top: 30px;
    margin-top: 30px;
    @media (max-width: 760px) {
      justify-content: center;
      flex-direction: column;
      align-items: center;
    }

    .img_title {
      display: flex;
      justify-content: space-between;
      align-items: center;
      width: 350px;
      @media (max-width: 760px) {
        width: 250px;
        margin-bottom: 20px;
      }
    }

    .count_price {
      display: flex;
      justify-content: space-between;
      align-items: center;
      width: 400px;
      @media (max-width: 760px) {
        width: 350px;
        justify-content: center;
      }
    }

    &-img {
      display: flex;
      align-items: center;
      margin-right: 15px;
      width: 10%;

      img {
        width: 80px;
        height: 80px;
      }
    }

    &-info {
      display: flex;
      flex-direction: column;
      justify-content: center;
      width: 60%;

      h3 {
        font-weight: bold;
        font-size: 22px;
        line-height: 27px;
        letter-spacing: 0.01em;
      }

      p {
        font-size: 18px;
        color: #8d8d8d;
        @media (max-width: 760px) {
          font-size: 15px;
        }
      }
    }

    &-count {
      display: flex;
      align-items: center;
      justify-content: space-between;
      width: 27%;

      &-minus {
        svg {
          path:first-of-type {
            display: none;
          }
        }

        &:disabled {
          background-color: rgba(239, 239, 239, 0.4);
          border-color: #787878;

          &:active {
            transform: none;
          }

          &:hover {
            background: transparent;

            svg path {
              fill: #787878;
            }
          }

          svg path {
            fill: #787878;
          }
        }
      }

      b {
        font-size: 22px;
      }
    }

    &-price {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 33%;

      b {
        font-weight: bold;
        font-size: 22px;
        letter-spacing: 0.01em;
      }
    }

    &-remove {
      display: flex;
      align-items: center;
      justify-content: flex-end;
      width: 4%;

      &-btn {
        background-color: rgba(239, 239, 239, 0.4);
        border-radius: 20px;
        border-color: #787878;

        &:hover,
        &:active {
          border-color: darken($color: $gray-line, $amount: 80);
          background-color: darken($color: $gray-line, $amount: 80);

          svg {
            fill: white;
          }
        }
      }
    }
  }

  &__bottom {
    margin: 50px 0;

    &-details {
      display: flex;
      justify-content: space-between;
      @media (max-width: 760px) {
        flex-direction: column;
        align-items: center;
        :nth-child(1) {
          margin-bottom: 10px;
        }
      }

      span {
        font-size: 22px;

        &:last-of-type {
          b {
            color: $orange;
          }
        }
      }
    }

    &-buttons {
      display: flex;
      justify-content: space-between;
      margin-top: 40px;

      .go-back-btn {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 210px;
        @media (max-width: 760px) {
          width: 150px;
        }

        border-color: darken($color: $gray-line, $amount: 10);

        span {
          color: darken($color: $gray-line, $amount: 20);
          font-weight: 600;
        }

        &:hover {
          background-color: darken($color: $gray-line, $amount: 90);
          border-color: darken($color: $gray-line, $amount: 90);

          span {
            color: $gray-line;
          }
        }

        svg {
          margin-right: 12px;

          path {
            fill: transparent;
            stroke-width: 2;
          }
        }
      }

      .pay-btn {
        font-size: 16px;
        font-weight: 600;
        width: 210px;
        padding: 16px;
        @media (max-width: 760px) {
          width: 150px;
        }
      }
    }
  }

  &--empty {
    margin: 80px auto;
    width: 620px;
    text-align: center;

    h2 {
      font-size: 36px;
      font-weight: 700;
      margin-bottom: 15px;
    }

    p {
      font-size: 20px;
      line-height: 145.4%;
      letter-spacing: 0.01em;
      color: #777777;
    }

    icon {
      position: relative;
      top: 2px;
    }

    img {
      display: block;
      width: 300px;
      margin: 45px auto 60px;
    }

    .button--black {
      padding: 12px 0 14px;
      width: 230px;
      margin: 0 auto;
      font-weight: 600;
      font-size: 18px;
    }
  }
}
